import React from 'react';
import { VictoryPie } from 'victory';
import '../App.scss'

function Footer({ setData, start, setStart, setTime, online, percent, socket, room }) {

    function startGraphic() {
        setStart(true)
        socket.emit('start')
    }
    
    function stop() {
        setStart(false)
        socket.off('graphic')
        socket.emit('finish')
    }

    function zero() {
        setTime(0)
        setData([{x: 1, y: 1}])
        setStart(false)
        socket.off('graphic')
        socket.emit('finish')
    }

    function plus() {
        socket.emit('plus', room)
    }

    function minus() {
        socket.emit('minus', room)
    }

    return (
        <>
        <div className='info__row'>
            <div className='label'>
                <p>ВСЕГО ПОЛЬЗОВАТЕЛЕЙ ОНЛАЙН: <span>{online}</span> чел.</p>
                <div className='diagram'>
                    <p>{percent}%</p>
                    <svg width={160} height={160}>
                        <VictoryPie
                            colorScale={["lightgray", percent < 50 ? 'red' : "#638ec9"]}
                            innerRadius={42}
                            data={[{y: 100 - percent}, {y: percent}]}
                            standalone={false}
                            width={160} height={160}
                            labels={(e) => ''}
                        />
                    </svg>
                </div>
            </div>

            <div>
                <button className='understand' onClick={() => plus()}>ПОНЯТНО</button>
                <button className='not_understand' onClick={() => minus()}>НЕ ПОНЯТНО</button>
            </div>
        </div>
        <div className='admin-button'>
            <button disabled={start} onClick={() => startGraphic()}>Начать</button>
            <button onClick={() => stop()}>Закончить</button>
            <button onClick={() => zero()}>Обнулить</button>
        </div>
        </>
    )
}


export default Footer

