import React, { useEffect } from 'react';
import '../App.scss'
import { VictoryArea, VictoryChart, VictoryCursorContainer, VictoryPie, VictoryTheme } from 'victory';
import { io } from 'socket.io-client'
import Header from './Header';
import Footer from './Footer';

const socket = io('https://test-test-123321.herokuapp.com/')

function Graphic() {
    let [data, setData] = React.useState([{x: 1, y: 1}])
    let [start, setStart] = React.useState(false)
    let [time, setTime] = React.useState(0)
    let [plusTime, setPlusTime]= React.useState(300)
    let [online, setOnline] = React.useState(0)
    let [percent, setPercent] = React.useState(0)
    let [scale, setScale] = React.useState(0)
    let room = 3

    useEffect(() => {
        socket.emit('connected', {
            room
        })
        socket.on('online', online => {
            setOnline(online);

        })
    }, [])

    useEffect(() => {
        if (start) {
            socket.on('percent', count => {
                setPercent(count)
                setData(prev => [...prev, {x: prev[prev.length - 1].x + 1, y: count}])
            })
            setData(p => {
                setTime(p1 => {
                    if (p[p.length - 1].x > p1 + plusTime) {
                        setData([{x: p[p.length - 1].x, y: p[p.length - 1].y}])
                        return p1 + plusTime
                    }
                    return p1
                })
                return [...p, {x: p[p.length - 1].x + 1, y: 0}]
            })
        }
    }, [start])

    return (
        <div>
            <Header setPlusTime={setPlusTime} setScale={setScale} />

            <VictoryChart 
                theme={VictoryTheme.material}
                domain={{ x: [time, time + plusTime], y: [-2, 100] }}
                width={1000}
                height={400}
                containerComponent={
                    <VictoryCursorContainer
                        cursorLabel={({ datum }) => `${Math.round(datum.x, 2)}, ${Math.round(datum.y, 2)}`}
                    />
                }>       
                <VictoryArea
                    style={{
                        data: { fill: percent < 50 ? 'red' : 'lightblue', stroke: percent < 50 ? 'darkred' : 'teal' }, 
                        parent: { margin: '20px', border: "1px solid #ccc"}
                    }} 
                    data={data}
                />
            </VictoryChart>

            <Footer 
                socket={socket} 
                setData={setData} 
                start={start} 
                setStart={setStart} 
                setTime={setTime} 
                online={online} 
                percent={percent} 
                room={room}
            />
        </div>
    )
}

//dk
export default Graphic

