import React, { useEffect } from 'react';
import '../App.scss'

function Header({ setPlusTime, setScale }) {
    
    function changeShowTime(e) {
        if (!e.target.classList.contains('row')) {
            for (let index = 0; index < e.target.closest('.row').children.length; index++) {
                e.target.closest('.row').children[index].classList.remove('active')
            }
          
            e.target.classList.add('active')
            setPlusTime(e.target.dataset.time)

            // if (e.target.dataset.time === '20') {
            //     setScale(new Array(5).fill(0).map((item, i) => (5 * i).toString()))
            // }

            // else if (e.target.dataset.time === '100') {
            //     setScale(new Array(21).fill(0).map((item, i) => (5 * i).toString()))
            // }

            // else if (e.target.dataset.time === '600') {
            //     setScale(new Array(5).fill(0).map((item, i) => (5 * i).toString()))
            // }

            // else if (e.target.dataset.time === '1200') {
            //     setScale(new Array(5).fill(0).map((item, i) => (5 * i).toString()))
            // }
        } 
    }


    return (
        <div onClick={(e) => changeShowTime(e)} className='row'>
            <button className='' data-time="60">1м</button>
            <button className='active' data-time="300">5м</button>
            <button className='' data-time="1800">30м</button>
            <button className='' data-time="3600">1ч</button>
        </div>
    )
}


export default Header

